import { useState } from "react";
import styles from "./Register.module.css"; // Import the updated CSS module

const Register = () => {
  const [status, setStatus] = useState(""); // Track selected status
  const [isLoggedIn, setIsLoggedIn] = useState(true); // Simulate login status
  
  const handleStatusClick = (status) => {
    setStatus(status); // Set the selected status
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    // Send the selected status to the API
    const response = await fetch("/api/sendWebhook", {
      method: "POST",
      body: JSON.stringify({ name: "John Doe", status }),
      headers: { "Content-Type": "application/json" },
    });

    if (response.ok) {
      alert("Status updated!");
    } else {
      alert("Error sending status.");
    }
  };

  const handleLogout = () => {
    setIsLoggedIn(false); // Set user as logged out (simulate logout)
    alert("You have logged out!");
    // You can add additional logic for actual logout, like clearing local storage or redirecting
  };

  if (!isLoggedIn) {
    return (
      <div className={styles.container}>
        <h1 className={styles.title}>You are logged out</h1>
        <button className={styles.submitButton} onClick={() => setIsLoggedIn(true)}>
          Login Again
        </button>
      </div>
    );
  }

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>Register Your Status</h1>
      <div className={styles.statusContainer}>
        <button
          onClick={() => handleStatusClick("Present")}
          className={`${styles.statusButton} ${status === "Present" ? styles.selected : ""}`}
        >
          Present
        </button>
        <button
          onClick={() => handleStatusClick("Late")}
          className={`${styles.statusButton} ${status === "Late" ? styles.selected : ""}`}
        >
          Late
        </button>
        <button
          onClick={() => handleStatusClick("Absent")}
          className={`${styles.statusButton} ${status === "Absent" ? styles.selected : ""}`}
        >
          Absent
        </button>
      </div>
      <button onClick={handleSubmit} className={styles.submitButton}>
        Submit
      </button>
      <button onClick={handleLogout} className={styles.logoutButton}>
        Logout
      </button>
    </div>
  );
};

export default Register;
